.section-footer {
  .section-container {
    padding: 3rem 2rem;

    display: flex;
    flex-direction: column;
    gap: 1rem;

    background-color: #e4d6c4;

    font-size: 0.9rem;
    letter-spacing: normal;

    .sf-info {
      span:first-child::after {
        content: "|";
        margin: 0 0.5rem;
      }
    }

    .sf-contact {
      display: flex;

      .sf-contact-mail {
        color: inherit;
        text-decoration: none;
        font-size: 0.8rem;
      }
    }
  }
}

@media screen and (min-width: 530px) {
  .section-footer {
    .section-container {
      padding: 3rem calc(10vw + 2rem);

      flex-direction: row;
      justify-content: space-between;
    }
  }
}