@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Merriweather:ital@0;1&family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;0,800;1,400&family=Work+Sans:wght@400;500;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 16px;

  --body-bg: #ECE3DA;
  --roasting-bg: #FFF;
  --region-title-color: #bf6d23;
  --region-p-color: #797370;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #383838; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

body {
  background-color: var(--body-bg);

  font-family: 'Montserrat';
  letter-spacing: 0.10rem;
  line-height: 2rem;
}

@media screen and (min-width: 530px) {
  .content {
    padding: 5rem 10vw 5rem;

    background-color: #FAEBD7;
  }
}